import Swiper from 'swiper/bundle';
import './../../block/animation';
import './../../block/sliderCursor';
import './../../../../../js-modules/cursorStatus';
import options from './../../block/swiperOptions';

window.account = {
    init: function() {
        account.swipers();
    },

    swipers: function() {
        ZoneObject.create('.section-account__nominations', {
            run: () => {
                // Карточки по направлениям
                new Swiper('.swiper-nominations', $.extend(options, {
                    slidesPerView: 'auto',
                    spaceBetween: 20,
                    breakpoints: {
                        768: {
                            slidesPerView: 'auto',
                            spaceBetween: 20
                        }
                    }
                }));
            }
        });
    }
}

$(account.init)